.d_backToTop {
  position: fixed;
  right: 0;
  bottom: 0;
  cursor: pointer;
  outline: none;
}

#EllipseOrangStroke {
  transition: all 0.3s;
}

@media screen and (max-width: 520px) {
  .d_backToTop {
    cursor: none;
  }

  .d_backToTop svg {
    width: 46px;
    height: 46px;
    display: none;
  }
}